import React from 'react'
import item from "./item.module.css";
import {NavLink} from "react-router-dom";
import dialogs from "../Dialogs.module.css";

const Item = (props) => {

    return (
        <div className={dialogs.users}>
            <img className={dialogs.avatar} src={props.src} />
            <NavLink to={props.id} className={itemData => itemData.isActive ? dialogs.active : item.item}>
                {props.name}
            </NavLink>
        </div>
    )
}

export default Item;